<template>
    <div class="container">
      <title-bar customeTitle="随手拍事件详情">
          <template v-slot:left>
            <span class="icon-left"><Icon name="arrow-left" @click="goBack"></Icon></span>
          </template>
      </title-bar>
      <div class="list-wrapper">
        <event-item v-for="item in itemDataList" :key="item" @click="itemClick(item.isSolve)" :itemData="item">{{item}}</event-item>
      </div>
    </div>
</template>

<script>
import TitleBar from '@/components/titleBar'
import eventItem from "@/pages/home/components/eventItem";
import {useRouter} from "vue-router";
import {Icon} from 'vant';
export default {
  name: "issueCallback",
  components:{
    TitleBar,
    eventItem,
    Icon
  },
  setup(){
    const router = useRouter();
    const itemClick = (isSolve) => {
      if(isSolve){
        router.push('/solvedetail');
      }else{
        router.push('/unsolvedetail');
      }
    }
    const goBack = () => {
        router.go(-1);
    }
    const itemDataList =[
      {
        isSolve:false,

      },
      {
        isSolve:true,

      },
      {
        isSolve:false,

      },
      {
        isSolve:false,

      },
      {
        isSolve:true,

      }
    ]
    return {
      itemDataList,
      itemClick,
      goBack
    }
  }
}
</script>

<style scoped lang="scss">
    @import "../../assets/styles/mixin";
    .container{
        width: 100%;
        height:100%;
    }

    .list-wrapper{
        height: calc(100% - 40px);
        overflow: auto;
    }

    .icon-left{
         position: absolute;
         left:px2rem(15);
    }
</style>